<template>
  <b-card>
    <b-row class="">
      <b-col cols="8">
        <vue-autosuggest
          ref="autocomplete"
          v-model="query"
          :suggestions="suggestions"
          :input-props="inputProps"
          :section-configs="sectionConfigs"
          :render-suggestion="renderSuggestion"
          :get-suggestion-value="getSuggestionValue"
          @input="fetchResults"
        />

      </b-col>
      <b-col cols="4">
        <b-button
          variant="primary"
          block
          @click="addParticipanteAct"
        >
          Seleccionar
        </b-button>
      </b-col>
    </b-row>
  </b-card>
</template>

<script>
import {
  BCard, BButton, BRow, BCol,
} from 'bootstrap-vue'
import { VueAutosuggest } from 'vue-autosuggest'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

export default {
  components: {
    VueAutosuggest,
    BRow,
    BCol,
    BCard,
    BButton,
  },
  data() {
    return {
      query: '',
      results: [],
      timeout: null,
      selected: null,
      debounceMilliseconds: 500,
      photosUrl: '/buscar-alumno',
      inputProps: {
        id: 'autosuggest__input_ajax',
        placeholder: 'Buscar persona por DNI, apellidos o nombres',
        class: 'form-control',
        name: 'ajax',
      },
      suggestions: [],
      sectionConfigs: {
        personas: {
          limit: 30,
          label: 'Estudiantes',
          onSelected: selected => {
            this.selected = selected.item
          },
        },
        // docentes: {
        //   limit: 30,
        //   label: 'Docentes',
        //   onSelected: selected => {
        //     this.selected = selected.item
        //   },
        // },
      },
    }
  },
  methods: {
    addParticipanteAct() {
      if (this.selected) {
        this.$emit('alumnoSeleccionado', this.selected)
        // this.$root.$emit('bv::show::modal', 'modal-agregar-cita', '#refModalAgregarCita')
        this.selected = null
        document.getElementsByName('ajax')[0].value = null
      } else {
        this.$toast({
          component: ToastificationContent,
          position: 'top-right',
          props: {
            title: 'Aviso!',
            icon: 'AlertTriangleIcon',
            variant: 'warning',
            text: 'Por favor seleccionar una persona.',
          },
        })
      }
    },
    fetchResults() {
      const { query } = this

      clearTimeout(this.timeout)
      this.timeout = setTimeout(() => {
        const personasPromise = this.$http.get(`${this.photosUrl}?query=${query}`, { params: { perPage: 50 } })

        Promise.all([personasPromise]).then(values => {
          // console.log('VALUES:', values)
          this.suggestions = []
          this.selected = null

          const alumnos = values[0].data.data
          // const docentes = values[0].data.data[1]
          // eslint-disable-next-line no-unused-expressions
          alumnos.length
            && this.suggestions.push({ name: 'personas', data: alumnos })
          // eslint-disable-next-line no-unused-expressions
          // docentes.length
          //   && this.suggestions.push({ name: 'docentes', data: docentes })
        }).catch(error => {
          this.$toast({
            component: ToastificationContent,
            props: {
              title: `${error.response.data.error}`,
              icon: 'EditIcon',
              variant: 'danger',
            },
          })
        })
      }, this.debounceMilliseconds)
    },
    renderSuggestion(suggestion) {
      return (
        <div>{suggestion.item.persona.apellidos}, {suggestion.item.persona.nombres} (<span class="text-danger">{suggestion.item.persona.numero_documento}</span>)</div>
      )
    },
    getSuggestionValue(suggestion) {
      const { item } = suggestion
      return `${item.persona.apellidos} ${item.persona.nombres} - ${item.persona.numero_documento}`
    },
  },
}
</script>

<style lang="scss">
@import '@core/scss/vue/libs/vue-autosuggest.scss';
</style>
